.message {
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.message>img {
    margin: 100px 0 10px;
}

.message>h1 {
    color: #1D2129;
    font-family: PingFang SC;
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    margin-bottom: 10px;
    text-align: center;
}

.message>p {
    color: #666;
    font-family: Inter;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.abolish {
    margin-top: 150px;
}

.abolish:hover {
    color: #329fa7;
    cursor: pointer;
    text-decoration: underline;
}