.advanced {
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.advanced_options {
    width: 97%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    margin-top: 5px;
    cursor: pointer;
    padding: 5px;
    border-radius: 5px;
    transition: .3s;
}

.advanced_options:hover {
    background-color: #dbeced !important;
}

.advanced_options>img {
    width: 30px;
    height: 30px;
    margin-right: 10px;
}

.advanced_options>p {
    color: #020202;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}