.env_image {
    width: 400px;
}

.start_ {
    display: block;
    width: 110px;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    color: #000;
    border: 2px solid #329fa7;
    margin: 20px 0;
    border-radius: 10px;
    cursor: pointer;
    transition: .3s;
}

.start_h1 {
    height: 50px;
}

.start_:hover {
    background-color: #329fa7;
    color: #fff;
}

.gettoken {
    overflow: hidden !important;
}

.start_p {
    height: 40px;
}

.max_height {
    height: 130px;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}