.prompt {
    text-align: center;
    font-size: 18px;
    color: #000;
}

.prompt>span {
    color: #329fa7;
}

.prompt_ul>li {
    margin: 0;
}

.prompt_ul>li>svg {
    margin-left: 30px;
    cursor: pointer;
    transition: .3s;
}

:where(.css-190m0jy).ant-select-single .ant-select-selector .ant-select-selection-item,
:where(.css-190m0jy).ant-select-single .ant-select-selector .ant-select-selection-placeholder {
    line-height: 50px !important;
}

:where(.css-190m0jy).ant-select-single:not(.ant-select-customize-input) .ant-select-selector {
    height: 50px !important;
}

.loader_spin {
    border: 1.5px solid #f3f3f3;
    border-top: 1.5px solid #329fa7;
    border-radius: 50%;
    height: 80%;
    aspect-ratio: 1/1;
    animation: spin 1s linear infinite;
    margin: 0 auto;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.ant-btn {
    width: 100px;
}