.progress_box {
    width: 100%;
    height: 140px;
    background-color: #329FA7;
    display: flex;
    align-items: center;
    justify-content: space-between;
    overflow: hidden;
}

.progress_ul {
    width: 800px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    list-style: none;
    margin-right: 50px;
}

.progress_box>img {
    margin-left: 50px;
}

.progress_ul>li {
    flex: 1;
    width: 100%;
    height: 100%;
    position: relative;
}

.progress_ul>li>.img_box {
    width: 50px;
    height: 50px;
    position: absolute;
    top: 50%;
    left: 50%;
    background-color: #329FA7;
    transform: translate(-50%, -50%);
    z-index: 2;
}

.progress_ul>li>.img_box>img {
    position: relative;
    z-index: 100;
    width: 100%;
    height: 100%;
}

.progress_line {
    width: 100%;
    height: 3px;
    background-color: #d1d1d1;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
}

.progress_ul>li>p {
    position: absolute;
    left: 50%;
    bottom: 10%;
    transform: translateX(-50%);
    color: #FFF;
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.last_li>.progress_line {
    width: 50%;
    right: 50%;
    transform: translate(0%, -50%);
}

.fast_li>.progress_line {
    width: 50%;
    left: 0;
    transform: translate(0%, -50%);
}

.logo_head {
    margin-left: 100px;
}