.mirroring_box {
    width: 100%;
    height: 100%;
    display: flex;
    overflow: hidden;
}

.left_os {
    width: 25%;
    overflow-y: auto;
    overflow-x: hidden;
    background-color: #f9f9f9;
}

.right_os_info {
    width: 75%;
    overflow-y: auto;
    overflow-x: hidden;
    padding: 20px;
    border-left: 2px solid #329FA7;
}

.left_os>p {
    width: 90%;
    height: 50px;
    margin: 10px auto;
    background-color: #fff;
    border-radius: 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #000;
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    cursor: pointer;
    padding: 0 5px;
    position: relative;
    transition: .3s;
}


.left_os>p:hover {
    background-color: #dbeced;
    color: #329FA7;
}

.left_os>.color329fa7 {
    background-color: #dbeced;
    color: #329FA7;
}


.icons_box>img {
    height: 50px;
    margin: 20px 0;
}

.right_os_info p {
    color: #333;
    font-family: Raleway;
    font-size: 22px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin-bottom: 10px;
    margin-right: 20px;
}

.right_os_info h1 {
    font-size: 34px;
    font-weight: 600;
    margin-bottom: 20px;
    color: #329FA7;
}

.right_os_info>p>span {
    margin-left: 10px;
    color: #329fa7;
}

.p_info {
    display: flex;
    margin: 20px 0;
}

.icons_box {
    height: 100%;
    justify-content: space-between;
}

.icons_box,
.right_icons {
    display: flex;
    align-items: center;
}

.right_icons>img {
    margin-right: 10px;
    width: 30px;
    height: 30px;
    cursor: pointer;
    transition: .3s;
}

.right_icons>img:hover {
    transform: scale(1.2);
}

.textcenter_h1 {
    text-align: center !important;
    color: red !important;
    margin: 100px 0;
}

.icon_box {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.icon_box>p {
    color: #329FA7;
    text-align: center;
    font-size: 18px;
    margin-top: 20px;
}

.os_icon {
    height: 80px;
}

.icons_box_2 {
    display: flex;
    align-content: center;
    justify-content: space-between;
}