.config {
    display: block;
    justify-content: space-between;
    background-color: #fff;
}

.config_pc_ul,
.config_wifi_ul {
    width: 100%;
    overflow: hidden;
}

.config_pc_ul {
    padding-bottom: 20px;
    border-bottom: 1px solid #000;
}

.config_wifi_ul {
    margin-top: 20px;
}

.config_pc_ul>li,
.config_wifi_ul>li {
    background-color: #f9f9f9;
    margin-bottom: 10px;
    border-radius: 10px;
    padding: 5px 10px;
}

.config_pc_ul>li,
.wifi_icon_box {
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.wifi_icon,
.pc_icon {
    width: 40px;
    height: 40px;
    margin-right: 20px;
}

.text_info {
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.config_wifi_ul>li {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    padding: 5px;
}

.text_info {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.wifi_icon_box>h2,
.text_info h2 {
    color: #000;
    font-family: Inter;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.text_info p {
    color: #999;
    font-family: Inter;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.setting_btn {
    border-radius: 10px;
    width: 100px;
    height: 35px;
    color: #329FA7;
    font-weight: 400;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #329FA7;
    cursor: pointer;
    font-size: 18px;
}

.password {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding-bottom: 5px;
}

.passward_input {
    margin-left: 50px;
    height: 45px;
    font-size: 20px;
}

.pas_btn {
    height: 45px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    border: none;
    width: 130px;
    background-color: #329FA7;
    color: #fff;
    margin: 0 50px;
    cursor: pointer;
    font-size: 20px;
}

.wifi_icon_box>h2 {
    cursor: pointer;
}

.wifi_icon_box>h2:hover {
    color: #329FA7;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.loadingAnimation {
    width: 100%;
    height: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.loadingAnimation>h2 {
    font-size: 24px;
    color: #999;
    margin-bottom: 10px;
}

.animation_ele {
    width: 50px;
    height: 50px;
    border: 4px solid #f3f3f3;
    border-top: 4px solid #329FA7;
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

.message_p {
    text-align: center;
    font-size: 16px;
    color: red;
}

.switchBtn {
    display: flex;
    align-items: center;
    margin-top: 15px;
}

.switchBtn>span {
    font-size: 20px;
    font-weight: 600;
    margin-right: 5px;
}