.content {
    width: 1300px;
    height: 800px;
    position: fixed;
    top: 50%;
    left: 50%;
    background-color: #fff;
    transform: translate(-50%, -50%);
    border-radius: 10px;
    display: flex;
    align-items: center;
    flex-direction: column;
    overflow: hidden;
    box-shadow: 0 5px 15px rgba(0, 0, 0, .4);
}

.content>h1 {
    color: #333;
    font-family: Inter;
    font-size: 34px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-align: center;
    margin: 0px auto 50px;
}

.content>p {
    color: #666;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.content>img {
    width: 150px;
    height: 150px;
    margin: 150px auto 20px;
}

.home_select {
    margin-top: 30px;
    width: 350px !important;
}

.next {
    margin-top: 200px;
    display: flex;
    width: 100px;
    height: 42px;
    padding: 5px;
    font-size: 20px;
    justify-content: center;
    color: #fff;
    align-items: center;
    border-radius: 5px;
    background-color: #329FA7;
    cursor: pointer;
    text-decoration: none;
}