.download {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.download>h2 {
    color: #333;
    font-family: Inter;
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    margin-top: 100px;
    margin-bottom: 30px;
}

.download>p {
    margin-top: 20px;
    font-size: 18px;
}

.download>p {}

.download_line {
    display: flex;
    align-items: center;
}

.line {
    width: 500px;
    height: 12px;
    background-color: #e5e6eb;
    border-radius: 10px;
    position: relative;
    z-index: 10;
    margin-right: 10px;
}

.line>div {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    background-color: #00b42a;
    z-index: 11;
    border-radius: 10px;
    transition: .3s;
}

.log_box {
    width: 90%;
    margin: 0 auto;
    text-align: left;
    height: 140px;
    overflow-y: auto;
}

@keyframes progressAnimation {
    0% {
        left: 0%
    }

    10% {
        left: 10%;
    }

    20% {
        left: 20%;
    }

    30% {
        left: 30%;
    }

    40% {
        left: 40%;
    }

    50% {
        left: 50%
    }

    60% {
        left: 60%;
    }

    70% {
        left: 70%;
    }

    80% {
        left: 80%;
    }

    90% {
        left: 90%;
    }

    100% {
        left: 100%;
    }
}

.progress-bar {
    width: 500px;
    height: 12px;
    background-color: #00b42a;
    border-radius: 10px;
    overflow: hidden;
    position: relative;
}

.progress {
    position: absolute;
    top: 50%;
    left: 0;
    border-radius: 10px;
    transform: translateY(-50%);
    height: 100%;
    width: 20%;
    background-color: rgb(235, 167, 66);
    animation: progressAnimation2 1s infinite;
}


@keyframes progressAnimation2 {
    0% {
        left: 0%;
    }

    100% {
        left: 100%;
    }
}