.network_box {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    width: 1000px;
    height: 500px;
    margin-top: 50px;
    background-color: #f9f9f9;
    overflow-y: auto;
    overflow-x: hidden;
    border-radius: 10px;
}

.network_box>h1 {
    color: #000;
    font-family: Inter;
    font-size: 32px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.network_box>h2 {
    font-family: PingFang SC;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 22px;
    margin: 30px 0 10px;
}


.network_box>p {
    color: #86909C;
    text-align: center;
    font-family: PingFang SC;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 22px;
}

.btns {
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.btns>div {
    color: #329fa7;
    font-family: Inter;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    min-width: 80px;
    height: 45px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    cursor: pointer;
    border-radius: 10px;
    border: 2px solid #329fa7;
    margin: 0 15px;
    background-color: #fff;
    transition: .3s;
    overflow: hidden;
    padding: 0 10px;
}

.btns>div:hover {
    color: #fff;
    background-color: #329fa7;
}

.btns>div>svg {
    margin: 0 5px;
}

.radar {
    font-size: 32px;
    width: calc(8em + 1.5em);
    height: calc(8em + 1.5em);
    position: relative;
    border-radius: 50%;
    border: 2px solid #329fa7;
    background-color: #fff;
}

.radar::before {
    content: '';
    position: absolute;
    width: calc(9.5em / 2);
    height: calc(9.5em /2);
    background: linear-gradient(45deg,
            hsla(0, 0%, 0%, 0) 50%,
            #329fa7 100%);
    border-radius: 100% 0 0 0;
    top: calc(0em / 2);
    left: calc(0em / 2);
    transform-origin: right bottom;
    animation: scanning 1.5s linear infinite;
}

@keyframes scanning {
    to {
        transform: rotate(1turn);
    }
}

.network_h2 {
    text-align: center;
    font-size: 26px;
    font-weight: 400;
    margin-top: 10px;
}


.btns>.index_btn {
    color: #fff;
    background-color: #4da4ab;
}


svg>path {
    transition: .3s;
}

.btns>div:hover svg>path {
    fill: #fff;
}


.network_box::-webkit-scrollbar {
    width: 6px;
    height: 10px;
}

/* 设置滚动条的背景颜色 */
.network_box::-webkit-scrollbar-track {
    background-color: #ccc;
    border-radius: 5px;
}

/* 设置滚动条的拖动块的样式 */
.network_box::-webkit-scrollbar-thumb {
    background-color: #329fa7;
    border-radius: 5px;
}