.content_contexts {
    width: 100%;
    height: 70%;
    background: url(../../static/contexts.webp) no-repeat;
    background-size: 100%;
    position: relative;
}

.protocols_mark {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .6);
    position: relative;
    z-index: 100;
    overflow: hidden;
}

.content_contexts>img {
    position: absolute;
    left: 20px;
    bottom: 20px;
    z-index: 10;
}

.flex_text {
    display: flex;
    align-items: center;
    margin: 50px 0;
}

.flex_text>p {
    cursor: pointer;
}

.flex_text>p:hover {
    text-decoration: underline;
}

.start_a {
    margin-top: 50px;
    margin-bottom: 20px;
}

.protocols_mark>div {
    padding: 0 50px;
    height: 100%;
    overflow-y: auto;
}

.protocols_mark>div::-webkit-scrollbar {
    width: 6px;
    height: 10px;
}

/* 设置滚动条的背景颜色 */
.protocols_mark>div::-webkit-scrollbar-track {
    background-color: #ccc;
    border-radius: 5px;
}

/* 设置滚动条的拖动块的样式 */
.protocols_mark>div::-webkit-scrollbar-thumb {
    background-color: #329fa7;
    border-radius: 5px;
}


.protocols_mark>div>h1 {
    color: #FFF;
    font-family: Inter;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-align: center;
    margin: 20px 0;
}

.protocols_mark>div>p {
    color: rgba(255, 255, 255, 0.90);
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 120%;
    margin-bottom: 15px;
}

.indent2 {
    text-indent: 2em;
}

.close {
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
}

.titlep_max {
    margin-top: 50px;
}