    flex-direction: column;
}

.warnings>img {
    width: 100px;
    height: 100px;
    margin-top: 100px;
}

.warnings>p {
    color: var(--text-5, #1D2129);
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    margin-top: 20px;
}