.setting_box{
    width: 100%;
    height: 100%;
}

.head_icon{
    width: 100%;
    padding: 20px;
    padding-left: 100px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-bottom: 1px solid #ccc;
}
.head_icon>h2{
    color: #000;
    font-family: Inter;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.inputs{
    width: 80%;
    margin: 20px auto;
    display: flex;
    flex-direction: column;
}

.inputs>li{
    display: flex;
    align-items: center;
    margin-bottom: 40px;
}
:where(.css-dev-only-do-not-override-190m0jy).ant-select-single:not(.ant-select-customize-input) .ant-select-selector {
    height: 50px;
}
:where(.css-dev-only-do-not-override-190m0jy).ant-select-single .ant-select-selector .ant-select-selection-item{
    line-height: 50px;
    font-size: 18px;
}
.input_text{
    width: 650px;
    height: 50px;
    font-size: 18px;
}

.inputs>li>span{
    text-align: right;
    width: 100px;
    font-size: 18px;
    margin-right: 50px;
}